import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  ColumnsProps,
  DefaultSortProps,
  enumColumn,
  FilterType,
  getExtraData,
  OrderType,
  referenceColumn,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMCG } from '../../../../dependencies/empresaMCGDependency';
import { CLASS_NAME, NotaExplicativaReferencia } from '../../../../models/dtos/mcg/notaExplicativaReferencia';
import { TIPO_NOTA_EXPLICATIVA_ENUM_VALUES } from '../../../../models/enumerated/mcg/tipoNotaExplicativaEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'referencia',
    order: OrderType.ASC,
  },
];

const NotaExplicativaReferenciaSearchView: FC<SearchViewProps<NotaExplicativaReferencia>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    referenceColumn({
      dataField: 'referencia',
      editable: false,
      sort: true,
      text: 'Referência',
    }),
    enumColumn(
      {
        dataField: 'tipo',
        editable: false,
        filter: {
          type: FilterType.SELECT,
        },
        sort: true,
        text: 'Tipo',
      },
      TIPO_NOTA_EXPLICATIVA_ENUM_VALUES
    ),
  ];

  return (
    <WindowComponent
      title="Nota Explicativa"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMCG(globalReducer.globalParameter)]}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'NotaExplicativaReferenciaEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default NotaExplicativaReferenciaSearchView;
