import { GlobalParameter } from '../models/dtos/ger/globalParameter';

export function hasEmpresaMCG(globalParameter: GlobalParameter): string | undefined {
  let result = null;
  if (!globalParameter.empresaMCG) {
    result =
      'A empresa selecionada não está configurada para o módulo Contábil. Para configurá-la acesse:\n' +
      'Módulo Contábil > Manutenção > Configurações > Empresa.';
  }
  return result;
}
